.products {

	&__layout {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__item {
		width: 350px;
		margin-bottom: 45px; } }

.product {
	box-shadow: 0 0 15px 3px $redLight;
	border-radius: 5px;

	&__title {
		padding: 15px;
		font-size: 20px;
		color: $blue;
		text-align: center; }

	&__img {
		width: 100%;
		height: auto;

		img {
			width: 100%;
			height: auto;
			max-height: 100%;
			object-fit: cover; } }

	&__descr {
		padding: 10px;
		text-align: center; } }
