.btn {
  display: block;
  width: 100%;
  padding: 12px 20px;

  text-align: center;
  color: $white;
  text-transform: uppercase;

  border-radius: 5px;
  background-color: $red;
  transition: background 0.5s ease;

  &:hover {
    background-color: $blue; }

  &--form {
    float: right; }

  &--icon {
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      fill: $white;
      margin-right: 5px;
      font-size: 20px; } }

  &-close {
    position: absolute;
    top: 10px;
    right: 10px;

    width: 20px;
    height: 20px;
    font-size: 0;

    background-color: $blue;
    opacity: 0.5;

    &:hover {
      opacity: 1; }

    &:active {
      opacity: 0.1; }

    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 7px;
      right: -3px;

      width: 22px;
      height: 4px;

      background-color: $red;
      border-radius: 2px; }

    &:before {
      transform: rotate(45deg); }

    &:after {
      transform: rotate(-45deg); } } }
