.bag {

	&__title {
		margin-bottom: 5px;
		text-align: center; }

	&__number {
		position: relative;
		display: block;
		font-size: 40px;
		text-align: center;

		svg {
			fill: $blue; }

		span {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			font-size: 18px;
			color: $white; } } }
