.features {
	font-family: 'Philosopher', sans-serif;

	&__layout {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__item {
		margin-bottom: 30px; } }

.feature {
	text-align: center;
	max-width: 350px;

	&__icon {
		position: relative;
		width: 100px;
		height: 100px;
		margin: 0 auto;
		background: linear-gradient(to bottom right, $red, $gold);
		// background-color: $gold
		border-radius: 50%;
		margin-bottom: 15px;
		font-size: 70px;
		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			fill: $white; } }

	&__title {
		font-size: 22px;
		color: $gray; } }
