.main-nav {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	width: 1000px;

	font-size: 18px;
	line-height: 1.2;
	background-color: $red;
	box-shadow: 0 4px 15px 0 rgba(0,0,0,0.7);

	z-index: 15;

	&__list {
		display: flex;
		justify-content: space-between; }

	&__item {
		position: relative;
		padding: 16px 20px;
		&:hover .main-nav__level2 {
			display: block; } }

	&__link {
		font-family: 'Roboto', sans-serif;
		color: $white;
		&:hover {
			border-bottom: 1px solid $white; }
		&:hover .main-nav__level2 {
			display: block; } }

	&__level2 {
		display: none;
		position: absolute;
		top: 53px;
		left: 0;
		background-color: $red;
		z-index: 10;

		li {
			display: flex;
			border-bottom: 1px solid $white;
			&:first-child {
				border-top: 1px solid $white; }
			&:hover {
				background-color: $blue; } }

		a {
			padding: 15px;
			font-size: 14px;
			font-family: 'Roboto', sans-serif;
			color: $white; } } }

