.tabs {

	&__header {
		display: flex;
		justify-content: center;
		margin-bottom: 25px; }

	&__title {
		position: relative;
		margin-right: 50px;
		font-size: 20px;
		color: $blue;
		font-weight: 300;
		text-transform: uppercase;


		cursor: pointer;

		&:hover, &.is-active {
			text-decoration: underline; }

		&:last-child {
			margin-right: 0; } } }
