.callback {
  width: 330px;
  padding: 20px;

  text-align: center;

  border: 1px solid $gold;
  border-radius: 5px;
  box-shadow: 0 0 15px 2px $gold;

  &__title {
    margin-bottom: 20px; }

  &__text {
    padding: 15px;

    font-size: 16px;
    color: $white;

    background-color: $red; }

  &__wrap {
    margin: 0 10px; }

  &__tel {
    height: 75px;
    padding: 15px;
    margin-bottom: 15px;

    border-bottom: 1px dashed $black;
    a {
      color: $black;
      font-size: 22px; }
    svg {
      fill: $gold;
      width: 50px;
      height: 50px;
      vertical-align: middle; } }

  p {
    display: inline-block;
    width: 260px; }

  &-form {
    &__input {
      width: 100%;
      padding: 10px;
      margin: 20px 0;
      border: 1px solid $gray;
      border-radius: 10px; } } }
