.product-card {
	display: block;

	color: inherit;

	background-color: #F8F8F8;
	box-shadow: 0 0 15px 1px rgba(0,0,0,0.5);
	border-radius: 5px;
	overflow: hidden;
	&:hover {
		box-shadow: 0 0 30px 3px rgba(0,0,0,0.5);

		& .product-card__img img {
			transform: scale(1.2); } }

	&__img {
		height: 300px;

		overflow: hidden;
		border: 15px solid #f8f8f8;
		img {
			width: 100%;
			height: auto;
			max-height: 100%;
			object-fit: cover;
			transition: all 0.3s ease; } }

	&__content {
		padding: 15px; }


	&__title {
		margin-bottom: 15px;
		font-size: 20px;
		color: $blue;
		text-align: center; }

	&__price {
		font-size: 20px;
		font-style: italic; }

	&__vendor-code {
		font-size: 12px;
		color: $gray;
		text-align: right;
		margin-bottom: 15px; } }


